import React, {useEffect} from 'react';
import './App.css';
import {AppMode, appSlice, blockList, DomStatus} from "./store/app";
import {useAppDispatch, useAppSelector} from "./store/store";
import {AuthStatus, userSelf} from "./store/user";
// @ts-ignore
import locomotiveScroll from "locomotive-scroll";
import UserForm from "./page/UserForm";
import Edit from "./page/Edit";
import Loading from "./lib/static/Loading";
import Control from "./Control";
import FeedbackForm from "./page/FeedbackForm";
import {Flex1} from "./lib/markup/markup";
import {bgPrimary, fCenter, fixHeight, jCenter} from "./lib/styles";

function App() {
    const dispatch = useAppDispatch()
    const {blocks, domStatus, mode} = useAppSelector(state => state.app)
    const {authStatus} = useAppSelector(state => state.user)
    const scrollRef = React.createRef();
    const admin = authStatus === AuthStatus.authorised && ['/admin', '/admin/'].includes(window.location.pathname)
    let scroll

    function changeMode() {
        dispatch(appSlice.actions.setAppMode(mode === AppMode.view ? AppMode.editor : AppMode.editor))
    }

    function adminActions() {
        if (!['/admin', '/admin/'].includes(window.location.pathname)) return
        if (authStatus === AuthStatus.initial) {
            dispatch(userSelf()) // проверка авторизован ли админ
        }
    }

    function openEditor(id: number) {
        if (!admin) return
        dispatch(appSlice.actions.openEditor(id))
    }

    useEffect(() => {
        adminActions()
        if (domStatus === DomStatus.initial) {
            dispatch(blockList())
        }

        if (domStatus === DomStatus.ready && blocks.length){
            try {
                scroll = new locomotiveScroll({
                    el: scrollRef.current,
                    smooth: true
                });
            } catch (e) {}


        }
    }, [authStatus, domStatus]);
    if (['/admin', '/admin/'].includes(window.location.pathname) && authStatus === AuthStatus.unauthorized) {
        return <UserForm/>
    }
    if (mode === AppMode.editor) {
        return <Edit/>
    }
    if (domStatus !== DomStatus.ready) {
        return  <Flex1 style={{marginTop: '30%', ...fCenter}}>
            <Loading/>
        </Flex1>
    }
    return (
        <div className="App">
            <Control admin={admin}/>
            {blocks.length ?
            <div
                className="scroll"
                // @ts-ignore
                ref={scrollRef}
            >
                <FeedbackForm/>
                {blocks.map((block, key) => {
                    return (
                        <div
                            key={key}
                            data-scroll
                            data-scroll-speed={block.speed}
                            data-scroll-position={block.position}
                            data-scroll-direction={block.direction}
                            className={admin ? 'blockAttention' : ''}
                            style={{
                                padding: 20,
                                verticalAlign: 'center',
                                cursor: admin ? 'pointer' : 'auto',
                                background: block.background,
                                backgroundImage: `url("${block.bg_image}")`,
                                ...fixHeight(block.height)
                            }}
                            dangerouslySetInnerHTML={{__html: block.html}}
                            onClick={() => openEditor(block.id)}
                        />
                    )
                })}
            </div> : null}

        </div>
    );
}

export default App;
