import TextArea from "../lib/fields/TextArea";
import {useState} from "react";
import {fCenter, fixHeight, flex1, jCenter, mb1, mt1, mv1} from "../lib/styles";
import {Col, Flex1} from "../lib/markup/markup";
import {BtnPrimary} from "../lib/buttons/Btn";
import {TextField} from "../lib/fields/inputs";
import {MainText} from "../lib/static/text";
import {useAppDispatch, useAppSelector} from "../store/store";
import {sendEmail} from "../store/app";

export default function () {
    const [message, setMessage] = useState('')
    const [email, setEmail] = useState('')
    const dispatch = useAppDispatch()
    const {emailSent} = useAppSelector(state => state.app)

    function send() {
        dispatch(sendEmail({message, email}))
    }

    if (emailSent) return <Flex1 style={fCenter}>
        <MainText>письмо отправлено</MainText>
    </Flex1>

    return <Flex1 style={fCenter}>
        <MainText>форма обратной связи</MainText>
        <TextArea
            value={message}
            style={{width: 200, height: 100, resize:'none'}}
            onChange={setMessage}
            placeholder={'написать письмецо себе'}
        />
        <MainText style={mt1}>надо ввести правильный email, валидацию не делал</MainText>
        <TextField
            style={mb1}
            placeholder={'email куда отправить'}
            value={email}
            onChange={setEmail}
        />
        <BtnPrimary
            title={'отправить'}
            onPress={send}
        />
    </Flex1>
}
