import {useState} from "react";
import {useAppDispatch} from "../store/store";
import {userLogin} from "../store/user";
import {fCenter, jCenter, mt4} from "../lib/styles";
import {Col, Flex1} from "../lib/markup/markup";
import {TextField} from "../lib/fields/inputs";
import {BtnPrimary} from "../lib/buttons/Btn";

export default function (): JSX.Element | null {
    const [username, setUsername] = useState('')
    const [password, setPassword] = useState('')
    const dispatch = useAppDispatch()

    function login() {
        dispatch(userLogin({username, password}))
    }

    return (
        <Flex1 style={{...fCenter, ...mt4}}>
            <Col>
                <TextField
                    placeholder={'username'}
                    onChange={setUsername}
                    value={username}
                />
                <TextField
                    placeholder={'password'}
                    onChange={setPassword}
                    value={password}
                />
                <BtnPrimary title={'войти'} onPress={login}/>
            </Col>
        </Flex1>
    )
}
