import {field, fixHeight, fRow} from "../styles";
import {ChangeEvent, createRef, useRef} from "react";
import {BtnPrimary} from "../buttons/Btn";
import {faImage} from "@fortawesome/free-solid-svg-icons";

interface IField {
    label?: string
    onChange?: (arg0: any) => void
    style?: object
    visible?: boolean
}

interface ITextField extends IField {
    placeholder?: string
    maxLength?: number
    value?: string
}


export function TextField({
                              label = '',
                              placeholder = 'placeholder',
                              onChange = () => {
                              },
                              style = {},
                              visible = true,
                              maxLength = 100,
                              value = ''
                          }: ITextField): JSX.Element | null {
    if (!visible) return null
    return <label>{label ? label + ': ' : ''}
        <input
            style={{...field, ...style}}
            placeholder={placeholder}
            onChange={e => onChange(e.target.value)}
            maxLength={maxLength}
            value={value}
        />
    </label>
}

interface INumberField extends IField {
    value: number
    max?: number
    min?: number
    step?: number
}

export function NumberField({
                                label = '',
                                visible = true,
                                onChange = () => {
                                },
                                value = 0,
                                style = {},
                                min = 0,
                                max = 999999,
                                step = 1
                            }: INumberField): JSX.Element | null {
    if (!visible) return null
    return <label style={{whiteSpace: 'nowrap'}}>{label ? label + ': ' : ''}
        <input
            style={{...field, ...style}}
            onChange={e => onChange(e.target.value)}
            type={'number'}
            value={value}
            max={max}
            min={min}
            step={step}
        />
    </label>
}

interface IColorField extends IField {
    value?: string
}

export function ColorField({
                               label = '',
                               visible = true,
                               onChange = () => {
                               },
                               value = '#000',
                               style = {},
                           }: IColorField): JSX.Element | null {
    if (!visible) return null
    return <label style={{whiteSpace: 'nowrap'}}>{label ? label + ': ' : ''}
        <input
            style={{...field, ...fixHeight(field.height + 8), ...style}}
            onChange={e => onChange(e.target.value)}
            type={'color'}
            value={value}
        />
    </label>
}

interface IFileField extends IField {
    value?: any
    accept?: string
}

export function FileField({
                              label = '',
                              visible = true,
                              onChange = () => {
                              },
                              value = '',
                              accept = "image/*",
    style={}
                          }: IFileField): JSX.Element | null {
    if (!visible) return null

    function uploadFile(e: ChangeEvent<HTMLInputElement>) {
        const fileList = e.target.files
        if (!fileList) return
        onChange(fileList[0])
    }

    function openLoader() {
            // @ts-ignore
        inputRef.current.click()
    }
    const inputRef = createRef()

    return <label style={{whiteSpace: 'nowrap'}}>{label ? label + ': ' : ''}
        <BtnPrimary
            icon={faImage}
            style={style}
            onPress={openLoader}
        />
        <input
            // @ts-ignore
            ref={inputRef}
            hidden={true}
            type={"file"}
            accept={accept}
            multiple={false}
            onChange={uploadFile}
            value={value ? value: undefined}
        />
    </label>
}
