import {createAsyncThunk, createSlice, PayloadAction} from "@reduxjs/toolkit";
import {DELETE, GET, PATCH, POST} from "./axios";
import {getObjectIndexById} from "../lib/sugar";


export enum BlockStatus {
    ready,
    updating,
    error
}

export enum EffectDirection {
    vertical = 'vertical',
    horizontal = 'horizontal'
}

export enum EffectPosition {
    top='top',
    bottom='bottom',
    left='left',
    right='right'
}

export interface IBlockApi {
    id: number
    number: number
    background: string
    bg_image: string
    html: string
    position: EffectPosition
    direction: EffectDirection
    speed: number
    height: number
}

export interface IBlock extends IBlockApi {
    status: BlockStatus
}

export enum DomStatus {
    initial,
    loading,
    updating,
    ready,
    error
}

export enum AppMode {
    view,
    loginForm,
    editor
}

interface IAppState {
    mode: AppMode
    domStatus: DomStatus
    blocks: IBlock[]
    currentBlockId: number
    emailSent: boolean
}

export const initialAppState: IAppState = {
    mode: AppMode.view,
    domStatus: DomStatus.initial,
    blocks: [],
    currentBlockId: -1,
    emailSent: false
}

export const blockList = createAsyncThunk(
    'app/loadApp',
    async () => {
        const response = await GET<IBlockApi[]>('blocks/', {}, false)
        return response.data
    }
)

export const createBlock = createAsyncThunk(
    'app/createBlock',
    async (args: object) => {
        const response = await POST<IBlockApi>('blocks/', args)
        return response.data
    }
)

interface IBlockPatch {
    id: number
    args: object
}

interface IFileUpload {
    id: number | null
    file: any
}

export const uploadBackGroundImage = createAsyncThunk(
    'app/uploadFile',
    async ({id, file}: IFileUpload) => {
        const args = new FormData()
        args.append('file', file)
        const response = await PATCH(`blocks/${id}/bg_image/`, args)
        return [response.data, id]
    }
)

interface ISend {
    message: string
    email: string
}

export const sendEmail = createAsyncThunk(
    'app/sendEmail',
    async ({message, email}: ISend) => {
        const response = await POST(`users/send_email/`, {message, email})
        return response.data
    }
)

export const appSlice = createSlice({
    name: 'app',
    initialState: initialAppState,
    reducers: {
        setAppMode: (state, action: PayloadAction<AppMode>) => {
            state.mode = action.payload
        },
        deleteBlock: (state, action: PayloadAction<number>) => {
            const index = getObjectIndexById(state.blocks, action.payload)
            DELETE(`blocks/${action.payload}/`)
            state.blocks.splice(index, 1)
        },
        patchBlock: (state, action: PayloadAction<IBlockPatch>) => {
            const {args, id} = action.payload
            const index = getObjectIndexById(state.blocks, id)
            PATCH(`blocks/${id}/`, args)
            state.blocks[index] = {...state.blocks[index], ...args}
        },
        openEditor: (state, action: PayloadAction<number>) => {
            state.currentBlockId = action.payload
            state.mode = AppMode.editor
        }
    },
    extraReducers: {
        [blockList.pending.type]: state => {
            state.domStatus = DomStatus.loading
        },
        [blockList.rejected.type]: state => {
            state.domStatus = DomStatus.error
        },
        [blockList.fulfilled.type]: (state, action: PayloadAction<IBlockApi[]>) => {
            state.domStatus = DomStatus.ready
            state.blocks = []
            for (let block of action.payload) {
                state.blocks.push({...block, status: BlockStatus.ready})
            }
        },

        [createBlock.pending.type]: state => {
            state.domStatus = DomStatus.updating
        },
        [createBlock.rejected.type]: state => {
            state.domStatus = DomStatus.error
        },
        [createBlock.fulfilled.type]: (state, action: PayloadAction<IBlockApi>) => {
            state.blocks.push({...action.payload, status: BlockStatus.ready})
            state.blocks.sort((a, b) => {
                return a.number > b.number ? 1 : -1
            })
            state.domStatus = DomStatus.ready
        },

        [uploadBackGroundImage.pending.type]: state => {
            state.domStatus = DomStatus.updating
        },
        [uploadBackGroundImage.rejected.type]: state => {
            state.domStatus = DomStatus.error
        },
        [uploadBackGroundImage.fulfilled.type]: (state, action: PayloadAction<[{ path: string }, number]>) => {
            const [file, id] = action.payload
            const index = getObjectIndexById(state.blocks, id)
            state.blocks[index].bg_image = file.path
            state.domStatus = DomStatus.ready
        },

        [sendEmail.fulfilled.type]: (state) => {
            state.emailSent = true
        }
    }
})















