import {Row} from "./lib/markup/markup";
import {jBetween, mh2} from "./lib/styles";
import {BtnPrimary} from "./lib/buttons/Btn";
import {MainText} from "./lib/static/text";
import {useAppDispatch} from "./store/store";
import {createBlock} from "./store/app";

// @ts-ignore
export default function ({admin}) {
    if (!admin) return null
    const dispatch = useAppDispatch()
    function addBlock() {
        dispatch(createBlock({html: 'новый блок'}))
    }

    const logout = () => {
        if (window.confirm('выйти из админки?')) {
            dispatch({type: 'logout'})
            window.location.href = '/'
        }
    }

    return <Row style={jBetween}>
        <BtnPrimary
            title={'добавить блок'}
            onPress={addBlock}
        />
        <MainText style={mh2}>кликните на блоке для редактирования</MainText>
        <BtnPrimary
            title={'выйти'}
            onPress={logout}
        />
    </Row>
}
