// @ts-ignore
import {Editor} from "react-draft-wysiwyg";
import "react-draft-wysiwyg/dist/react-draft-wysiwyg.css";
import {AppMode, appSlice, EffectDirection, EffectPosition, IBlock, uploadBackGroundImage} from "../store/app";
import {useState} from "react";
import {ContentState, convertToRaw, EditorState} from "draft-js";
// @ts-ignore
import draftToHtml from 'draftjs-to-html';
// @ts-ignore
import htmlToDraft from 'html-to-draftjs';
import {useAppDispatch, useAppSelector} from "../store/store";
import {getObjectIndexById} from "../lib/sugar";
import {Col} from "../lib/markup/markup";
import {BtnPrimary, BtnSecondary} from "../lib/buttons/Btn";
import {ColorField, FileField, NumberField} from "../lib/fields/inputs";
import {MainText} from "../lib/static/text";
import {fixWidth, m1, m2, mt2} from "../lib/styles";
import {faTrash} from "@fortawesome/free-solid-svg-icons";
import Select from "../lib/fields/Select";

export default function () {
    const {currentBlockId, blocks} = useAppSelector(state => state.app)
    const index = getObjectIndexById(blocks, currentBlockId)
    const block = blocks[index]
    const contentBlock = htmlToDraft(block.html);
    const contentState = ContentState.createFromBlockArray(contentBlock.contentBlocks);
    const initialState = EditorState.createWithContent(contentState);
    const [editorState, onEditorStateChange] = useState(initialState)
    const dispatch = useAppDispatch()

    function saveAttr(args: object) {
        dispatch(appSlice.actions.patchBlock({id: block.id, args}))
    }
    function closeEditor() {
        dispatch(appSlice.actions.setAppMode(AppMode.view))
    }

    function save(state: any) {
        const html = draftToHtml(convertToRaw(state.getCurrentContent()))
        const args = {html}
        dispatch(appSlice.actions.patchBlock({id: block.id, args}))
        onEditorStateChange(state)
    }

    return <Col>
        <BtnPrimary title={'закрыть редактор'} onPress={closeEditor}/>
        <Editor
            editorState={editorState}
            onEditorStateChange={save}
            toolbarCustomButtons={[
                <BackgroundLabel/>,
                <BackgroundColor {...block}/>,
                <BackgroundImage {...block}/>,
                <EffectTitle/>,
                <SelectPosition position={block.position} save={saveAttr}/>,
                <SelectDirection direction={block.direction} save={saveAttr}/>,
                <SetSpeed speed={block.speed} save={saveAttr}/>,
                <SetHeight height={block.height} save={saveAttr}/>,
                <DeleteBlock {...block}/>
            ]}
            toolbar={{
                inline: {inDropdown: true},
                list: {inDropdown: true},
                textAlign: {inDropdown: true},
                link: {inDropdown: true},
                history: {inDropdown: true},
                // image: {uploadCallback: uploadImageCallBack, alt: {present: true, mandatory: true}},
            }}
        />
    </Col>
}

function BackgroundLabel() {
    return <MainText style={m2}>фон:</MainText>
}

function BackgroundColor(props: IBlock) {
    const dispatch = useAppDispatch()

    function setColor(value: string) {
        const args = {background: value}
        dispatch(appSlice.actions.patchBlock({id: props.id, args}))
    }

    return (
        <ColorField
            style={{...m1, ...fixWidth(45)}}
            value={props.background}
            onChange={setColor}
        />
    );
}

function BackgroundImage(props: IBlock) {
    const dispatch = useAppDispatch()

    function setImage(value: string) {
        dispatch(uploadBackGroundImage({id: props.id, file: value}))
    }

    let label = ''
    if (props.bg_image) {
        const parts = props.bg_image.split('/')
        label = parts[parts.length - 1]
    }
    return (
        <FileField
            label={label}
            style={m1}
            onChange={setImage}
        />
    );
}

function DeleteBlock(props: IBlock) {
    const dispatch = useAppDispatch()

    function removeBlock() {
        if (window.confirm('удалить блок?')) {
            dispatch(appSlice.actions.deleteBlock(props.id))
            dispatch(appSlice.actions.setAppMode(AppMode.view))
        }
    }

    return <BtnPrimary
        style={m1}
        icon={faTrash}
        onPress={removeBlock}
    />
}

function EffectTitle(){
    return <MainText style={m2}>эффект:</MainText>
}

interface ISelectPosition {
    position: EffectPosition
    save: any
}

function SelectPosition ({position, save}:ISelectPosition) {
    const values = ['top', 'bottom', 'left', 'right']
    function changeValue() {
        const nextPosition = getNextValue(values, position)
        save({position: nextPosition})
    }

    return <BtnSecondary
        style={{...fixWidth(60), ...m1}}
        title={position}
        onPress={changeValue}
    />
    // селектор здесь не работает. Не кликается, не выпадает
    // return <Select
    //     onChange={position => save({position})}
    //     items={[
    //         {label: 'top', value: 'top'},
    //         {label: 'bottom', value: 'bottom'},
    //         {label: 'left', value: 'left'},
    //         {label: 'right', value: 'right'},
    //     ]}
    //     style={{...fixWidth(60), ...m1}}
    //     value={position}
    // />
}


interface ISelectDirection {
    direction: EffectDirection
    save: any
}

function getNextValue(array: string[], current: string) {
    for (let i=0; i<array.length; i++) {
        if (array[i] === current && i<array.length-1) return array[i+1]
    }
    return array[0]
}
function SelectDirection({direction, save}: ISelectDirection) {
    const values = ['vertical', 'horizontal']
    function changeValue() {
        const newDirection = getNextValue(values, direction)
        save({direction: newDirection})
    }

    return <BtnSecondary
        style={{...fixWidth(90), ...m1}}
        title={direction}
        onPress={changeValue}
    />
    // селектор здесь не работает. Не кликается, не выпадает
    // return <Select
    //     onChange={direction => save({direction})}
    //     items={[
    //         {label: 'vertical', value: 'vertical'},
    //         {label: 'horizontal', value: 'horizontal'},
    //     ]}
    //     style={{...fixWidth(90), ...m1}}
    //     value={direction}
    // />
}


interface ISelectSpeed {
    speed: number
    save: any
}

function SetSpeed({speed, save}: ISelectSpeed) {
    return <NumberField
        value={speed}
        style={{...fixWidth(40), ...m1}}
        onChange={speed => save({speed})}
    />
}

interface ISelectHeight{
    height: number
    save: any
}

function SetHeight({height, save}: ISelectHeight) {
    return <NumberField
        label={'высота'}
        value={height}
        style={{...fixWidth(40), ...m1}}
        onChange={height => save({height})}
    />
}
