interface ITextArea {
    label?: string
    onChange?: (arg0: any) => void
    style?: object
    visible?: boolean
    placeholder?: string
    value?: string
}

export default function ({
                              label = '',
                              placeholder = 'placeholder',
                              onChange = () => {
                              },
                              style = {},
                              visible = true,
                              value = ''
                          }: ITextArea): JSX.Element | null {
    if (!visible) return null
    return <textarea
            style={style}
            placeholder={placeholder}
            onChange={e => onChange(e.target.value)}
            value={value}
    />
}
